<template>
  <div>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Teacher
      </el-col>
      <el-col :span="10">
        {{course.teacherName}}
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Title
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.title" placeholder="Title"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Maximum
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.maximum" placeholder="Maximum" type="number"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Fee
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.fee" placeholder="Fee"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Description
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.description" placeholder="Description" type="textarea" :rows="3"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Days
      </el-col>
      <el-col :span="10">
        <div>
          <el-checkbox label="Monday" v-model="course.days_check[0]"></el-checkbox>
          <el-checkbox label="Tuesday" v-model="course.days_check[1]"></el-checkbox>
          <el-checkbox label="Wednesday" v-model="course.days_check[2]"></el-checkbox>
          <el-checkbox label="Thursday" v-model="course.days_check[3]"></el-checkbox>
        </div>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Grade
      </el-col>
      <el-col :span="10">
        <div>
          <el-checkbox label="K" v-model="course.grade_check[0]"></el-checkbox>
          <el-checkbox label="1" v-model="course.grade_check[1]"></el-checkbox>
          <el-checkbox label="2" v-model="course.grade_check[2]"></el-checkbox>
          <el-checkbox label="3" v-model="course.grade_check[3]"></el-checkbox>
          <el-checkbox label="4" v-model="course.grade_check[4]"></el-checkbox>
          <el-checkbox label="5" v-model="course.grade_check[5]"></el-checkbox>
          <el-checkbox label="6" v-model="course.grade_check[6]"></el-checkbox>
          <el-checkbox label="7" v-model="course.grade_check[7]"></el-checkbox>
          <el-checkbox label="8" v-model="course.grade_check[8]"></el-checkbox>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Room
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.room" placeholder="Room"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Remark
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.remark" placeholder="Remark"></el-input>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="5">
        <el-button type="primary" @click="edit">Edit</el-button>
      </el-col>
      <el-col :span="5">
      <el-button type="primary" @click="remove">Delete</el-button>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {
  mapState
} from 'vuex'
import {update, remove} from 'api/asa'
import {userValidation} from 'assets/js/mixin'
export default {
  name: 'HelloWorld',
  data () {
    return {
      course: {
        days_check: [false, false, false, false],
        grade_check: [false, false, false, false, false, false, false, false, false]
      },
      editable: true
    }
  },
  computed: {
    ...mapState(['currentASA'])
  },
  mixins: [userValidation],
  created () {
    console.log(this.currentASA)
    this.course.teacher = this.currentASA.teacher
    this.course.teacherName = this.currentASA.teacherName
    this.course.title = this.currentASA.title
    this.course.maximum = this.currentASA.maximum_num
    this.course.fee = this.currentASA.fee
    this.course.description = this.currentASA.description
    this.course.room = this.currentASA.room
    this.course.remark = this.currentASA.remark
    if (this.currentASA.days.indexOf('1') !== -1) {
      this.course.days_check.splice(0, 1, true)
    }
    if (this.currentASA.days.indexOf('2') !== -1) {
      this.course.days_check.splice(1, 1, true)
    }
    if (this.currentASA.days.indexOf('3') !== -1) {
      this.course.days_check.splice(2, 1, true)
    }
    if (this.currentASA.days.indexOf('4') !== -1) {
      this.course.days_check.splice(3, 1, true)
    }

    if (this.currentASA.grade.indexOf('k') !== -1) {
      this.course.grade_check.splice(0, 1, true)
    }
    if (this.currentASA.grade.indexOf('1') !== -1) {
      this.course.grade_check.splice(1, 1, true)
    }
    if (this.currentASA.grade.indexOf('2') !== -1) {
      this.course.grade_check.splice(2, 1, true)
    }
    if (this.currentASA.grade.indexOf('3') !== -1) {
      this.course.grade_check.splice(3, 1, true)
    }
    if (this.currentASA.grade.indexOf('4') !== -1) {
      this.course.grade_check.splice(4, 1, true)
    }
    if (this.currentASA.grade.indexOf('5') !== -1) {
      this.course.grade_check.splice(5, 1, true)
    }
    if (this.currentASA.grade.indexOf('6') !== -1) {
      this.course.grade_check.splice(6, 1, true)
    }
    if (this.currentASA.grade.indexOf('7') !== -1) {
      this.course.grade_check.splice(7, 1, true)
    }
    if (this.currentASA.grade.indexOf('8') !== -1) {
      this.course.grade_check.splice(8, 1, true)
    }
  },
  methods: {
    remove() {
      remove(this.currentASA.objectId).then((res) => {
        this.$router.back()
      })
    },
    edit() {
      if (this.editable) {
        this.editable = false
        let grade = ''
        for (let i in this.course.grade_check) {
          if (this.course.grade_check[i]) {
            if (i === '0') {
              grade += 'k'
            } else {
              grade += i
            }
          }
        }
        this.course.grade = grade
        let days = ''
        for (let i in this.course.days_check) {
          if (this.course.days_check[i]) {
            days += (parseInt(i) + 1)
          }
        }
        this.course.days = days

        if (this.course.title === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.maximum === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.fee === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.description === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.days === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.grade === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.room === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        update(this.currentASA.objectId, {
          days: this.course.days,
          description: this.course.description,
          fee: parseInt(this.course.fee),
          maximum_num: parseInt(this.course.maximum),
          remark: this.course.remark,
          room: this.course.room,
          title: this.course.title,
          grade: this.course.grade
        }).then((res) => {
          this.$router.back()
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



